	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Project Overview","depth":2},{"value":"Key Features","depth":2}],"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Frank AI App\"\n        title=\"\"\n        src=\"/static/frank1-f5e21632763b7d7248555d1ee092e5a8-53128.png\"\n        srcset=\"/static/frank1-f5e21632763b7d7248555d1ee092e5a8-7266b.png 375w,\n/static/frank1-f5e21632763b7d7248555d1ee092e5a8-0a3c1.png 750w,\n/static/frank1-f5e21632763b7d7248555d1ee092e5a8-53128.png 1500w,\n/static/frank1-f5e21632763b7d7248555d1ee092e5a8-29ec6.png 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Project Overview</h2>\n<p>Frank is a compassionate and insightful AI Therapist and Life Coach, offering portable and easily-accessible advice whenever and wherever users need it most. Through open dialogue, Frank offers guidance, encouragement, and support to help users accomplish their goals and overcome challenges in life. </p>\n<p>He is well-trained and equipped to help users combat symptoms of mood disorders, including depression and anxiety. Unlike traditional methods of therapy, Frank is there 24/7 to offer information and make suggestions specifically catered to each user and their personal needs. The more he interacts with you, the more he learns, and the better he can guide you on your own journey towards self-improvement.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 79.45%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/static/frank2-4035ccbd4f7c21fa95eec05827ebe3d0-53128.png\"\n        srcset=\"/static/frank2-4035ccbd4f7c21fa95eec05827ebe3d0-7266b.png 375w,\n/static/frank2-4035ccbd4f7c21fa95eec05827ebe3d0-0a3c1.png 750w,\n/static/frank2-4035ccbd4f7c21fa95eec05827ebe3d0-53128.png 1500w,\n/static/frank2-4035ccbd4f7c21fa95eec05827ebe3d0-29ec6.png 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Key Features</h2>\n<p>He is funny. In addition to being helpful, Frank knows how to incorporate tasteful humour into his responses. He is able to provide entertainment based on the individual, thus improving user-retention. Humour and light-heartedness are great tools for elevating mood and counteracting negative thoughts.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 79.45%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/static/frank3-3d54886da3216b34295a23ce2597bd69-53128.png\"\n        srcset=\"/static/frank3-3d54886da3216b34295a23ce2597bd69-7266b.png 375w,\n/static/frank3-3d54886da3216b34295a23ce2597bd69-0a3c1.png 750w,\n/static/frank3-3d54886da3216b34295a23ce2597bd69-53128.png 1500w,\n/static/frank3-3d54886da3216b34295a23ce2597bd69-29ec6.png 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>He is reliable. Frank helps individuals deal with problems as they present themselves. He is available to help 24/7 – no need to book an appointment or schedule a time to chat.</p>\n<p>He is compassionate. Frank has the ability to express sympathy. He knows life can be challenging, and cares about finding solutions to help you grow.</p>\n<p>He is intelligent. Frank knows how to problem solve, and is always able to suggest the most appropriate solution based on the situation and user. Not only will he provide you with the best solutions, he’ll take the time to educate you on them as well. He has a perfect memory, so anything you tell him, he’ll be able to recount later.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 79.45%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/static/frank4-09ddb9d8a7416c04add7561d5299440c-53128.png\"\n        srcset=\"/static/frank4-09ddb9d8a7416c04add7561d5299440c-7266b.png 375w,\n/static/frank4-09ddb9d8a7416c04add7561d5299440c-0a3c1.png 750w,\n/static/frank4-09ddb9d8a7416c04add7561d5299440c-53128.png 1500w,\n/static/frank4-09ddb9d8a7416c04add7561d5299440c-29ec6.png 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>View the full project at <a href=\"www.amandapape.com/frank\">www.amandapape.com/frank</a>.</p>","frontmatter":{"title":"Frank","graduate":"Amanda Pape","category":"UI/UX","category2":"Product Design","category3":"Brand Identity","image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/webfrank-31ae424438e96e373f93174f3b43d8ce-196f3.jpg","srcSet":"/static/webfrank-31ae424438e96e373f93174f3b43d8ce-1fcb0.jpg 375w,\n/static/webfrank-31ae424438e96e373f93174f3b43d8ce-0cd9a.jpg 750w,\n/static/webfrank-31ae424438e96e373f93174f3b43d8ce-196f3.jpg 1080w","sizes":"(max-width: 1080px) 100vw, 1080px"}}}}}},"pathContext":{"slug":"/work/frank/","name":"/Frank/"}}

